import axios from 'axios';
import qs from 'qs';
import NProgress from 'nprogress';
import 'nprogress/nprogress.css';

import { Message } from 'element-ui';

//  让请求在浏览器中允许跨域携带cookie
// axios.defaults.withCredentials = true;

// 使用自定义配置新建一个 axios 实例
const service= axios.create({
   // 基础的请求地址
    baseURL: 'http://localhost:5200',
   // 设置超时时间 5s
    timeout: 5000
});

// 请求拦截器，例如请求的时候在头部加上请求的token
service.interceptors.request.use(config => {
    // axios中POST请求变成OPTIONS处理
    if(config.method  === 'post'){
        config.data = qs.stringify(config.data);
    }
    let admin=config.url.slice(0,6);  //console.log("admin:",admin);
    if (localStorage.getItem('token') && admin==='/admin') {
        config.data+='&token='+localStorage.getItem('token'); //添加token
        // console.log(config.data);
    }
    NProgress.start();//显示进度条  
    return config;  //  有且必须有一个config对象被返回

}, error => {//   对请求错误做些什么
    console.log(error);
    return Promise.reject();
});

// 响应拦截器,例如判断服务器返回的状态，400，500啥的，其实超时可以写到这里面来，我分开写了一个比较直观
service.interceptors.response.use(
    response => {
        if (response.status === 200) {
            // console.log("response",response);
            try {
                if(response.data.data[0].code===10099){//token失效
                    Message.error({
                        message: '登录超时，请重新登录!'
                    });
                    setTimeout(() => {
                        localStorage.removeItem("token");  
                        location.reload();
                    }, 1000);
                   
                }
            } catch (error) {
               console.log(error); 
               
            }
           
            NProgress.done();//隐藏进度条
            return Promise.resolve(response.data);
        } else {
            return Promise.reject(response);
        }
    },
    // 服务器状态码不是200的情况,这些自定义（需要与后台商量返回）
    error => {
        if (  400 <= error.response.status <500) {
            console.error(error.response.status)
            // alert("用户信息过期，请重新登陆");
            // 清除token
            // localStorage.removeItem("token");
            // 跳转登录
            //setTimeout(() => {
            //   window.location.href = "/login";
            //}, 1000);
        } else {
            if (error.response.status >= 500) {
                alert("服务器开小差了，请稍后再试！");
            } else {
                alert("服务器开小差了，请稍后再试！");
                return Promise.reject(error)
            }
        }
    }
);
